.dealcard {
    border: 1px solid transparent;
    border-radius: 10px;
    position: relative;
    transition: all 1s 0s ease;

    .img {

        position: relative;

        span {
            position: absolute;
            top: 5%;
            right: 7%;
            height: 40px;
            width: 40px;
            font-size: 15px;
            background-color: #bbb;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .details {
        .price-part {
            .old-price{
                text-decoration: line-through;
            }
        }

        .title {
            font-size: 16px;
            color: #929191;

        }

        .icon-star {
            color: #FED700;

        }
        .text{
            font-size: 15px;
        }

        a {
            font-size: 15px;
            text-decoration: none;
            font-weight: bold;
        }
    }

    .icon-star {
        color: #FED700;
    }

    .btn-icon {
        width: 100%;
        max-height: 0;
        transition: max-height .7s ease-out;
        overflow: hidden;
        position: absolute;
        bottom: 0;

    }

    &:hover {

        box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.2);

    }

    &:hover .btn-icon {
        max-height: 100px;
        transition: max-height .5s ease-in;

    }


}

@keyframes shutter-in-top {
    0% {
        -webkit-transform: rotateX(-100deg);
        transform: rotateX(-100deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 0;
    }

    100% {
        -webkit-transform: rotateX(0deg);
        transform: rotateX(0deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 1;
    }
}