.blog-sidebar{
     h5{
          font-size:17px;
     }
     hr{
          width:40%;
          height:3px;
     }
     ul{
          padding-left: 0;
          li{
               list-style:none;
               a{
                    font-size:14px;
                    color:#FFCA2C;
                    // font-weight:bold;
               }
          }
     }
}
.blogs{
     .blogs-disc{
          margin: 7px 0;
          h3{
           margin: 20px 0;
           font-size:25px;
           font-weight: bold;
           &:hover{
                color:#FFCA2C;
           }
           }
          span{
               font-size: 14px;
               color:#a4a4a4;
          }
     } 
     p{
          width:80%;
          font-size: 15px;
          color:#a4a4a4;
     }   
}
