.navbars {
    padding: 20px 0px 40px;
    position: relative;
    margin-bottom: 50px;


    .navbar-item {
        // background-color: #fed700;

        display: flex;
        justify-content: space-around;
        align-items: center;

        .navbar-list-tab {
            display: none;
        }

    }

    .logo {
    
            
            padding: 0px 20px;
            .logo-title{
                font-size: 36px;
                font-weight: bold;
                text-decoration: none;
                color: black;
                span{
                    color: #ff0080;
                }
            }
    }

    .input-group {
        width: 40% !important;

        .dropdown-menu {
            z-index: 1100;
        }
    }

    .cart-icon {
        display: flex;
        justify-content: space-between;

        div {
            padding: 10px;
            margin: 0 10px;
        }

        .icon-list {
            display: none;
        }
    }

    .cart-dropdown {
        &:hover {
            .dropdown-cart {
                display: block !important;
            }
        }
        .btn-cart {
            background: inherit;
            .content-num {
                .num {
                    font-size: 12px !important;
                    font-weight: bold;
                }
            }
       
        }
        .dropdown-cart {
            display: none;
        }


    }

    .navbar-menu {
        display: flex;
        align-items: center;
        z-index: 1000;
        width: 100%;
        background:   #FFCA2C;
        box-shadow: 2px 6px 6px 2px rgba(0, 0, 0, 0.1);

        .navbar-list {
            display: flex;
            margin: auto;

            li {
                list-style: none;
                padding: 5px 15px;
                font-size: 16px;
                font-weight: bold;
                color: #fed700;
                cursor: pointer;
                position: relative;

                a {
                    text-decoration: none;
                    color: #fff;
                }

                &:hover {
                    transition: 1s ease;
                    box-sizing: border-box;
                    // outline: 1px solid #fed700;
                    box-shadow: 0px 2px 0px 0px #e91e63;

                    a {
                        transition: 1s ease;
                        color: #e91e63;

                    }

                    .dropdown_menu_shop {
                        display: block
                    }

                    .dropdown_menu_page {
                        display: block
                    }
                }

                .dropdown_menu_shop {
                    display: none;
                    position: absolute;
                    top: 100%;
                    left: 0;
                    background-color: #333333;
                    width: max-content;
                    outline: 1px solid white;
                    border-radius: 5px;

                    ul {
                        padding: 10px;

                        li {
                            list-style: none;
                            padding: 5px;
                            color: white;
                        }
                    }
                }

                .dropdown_menu_page {
                    display: none;
                    position: absolute;
                    top: 100%;
                    left: 0;
                    background-color: #333333;
                    width: max-content;
                    outline: 1px solid white;
                    border-radius: 5px;

                    ul {
                        padding: 10px;

                        li {
                            list-style: none;
                            padding: 5px;
                            color: white;
                        }
                    }
                }
            }
        }

        .cart-button {
            background: inherit;
        }
    }

    .cart-dropdown {
        position: relative;

        .dropdown-cart {
            width: 350px !important;
            position: absolute;
            top: 100%;
            right: 0;
            padding: 5px;
            transition: all 1s ease;
            background: white;
            z-index: 5000;
        }
    }

}

@media (max-width: 992px) {
    .navbar-menu {
        .logo {
            // display: inline-block !important;
            margin: 0;

            h2 {
                font-size: 22px;
            }
        }

        .navbar-list {
            display: flex;
            margin: auto;

            li {

                padding: 10px 5px !important;
                font-size: 13px !important;
                // font-weight: bold;
                color: #fed700;
                cursor: pointer;
                position: relative;

                a {
                    text-decoration: none;
                    color: #fff;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .navbars {
        padding: 10px 0px;
        margin-bottom: 10px;

        .navbar-item {
            position: relative;
            flex-direction: column;

            .logo {
                margin-bottom: 15px;

                h2 {
                    font-size: 35px;
                }
            }

            .input-group {
                width: 80% !important;
                margin-bottom: 30px !important;
            }

            .sign {
                display: none;
            }

            .cart-icon {
                width: 100%;
                align-items: center;

                .icon-list {
                    display: block;
                }

            }

            .navbar-menu {
                .logo {
                    // display: inline-block !important;
                    margin: 0;

                    h2 {
                        font-size: 25px;
                    }
                }

                .navbar-list {
                    li {
                        font-size: 12px !important;
                    }
                }
            }

            .active {
                display: block !important;
            }

            .navbar-list-tab {
                width: 100%;
                position: absolute;
                top: 100%;
                z-index: 3000;
                flex-direction: column;
                background-color: #f8f9fa;
                box-shadow: 2px 6px 6px 2px rgba(0, 0, 0, 0.1);
                border-radius: 10px;
                padding: 3px 0;

                li {
                    list-style: none;
                    padding: 10px 10px;
                    text-align: center;

                    &:hover {
                        border: 1px solid rgb(5, 2, 2);
                        border-radius: 10px;
                    }

                    a {
                        text-decoration: none;
                        color: black;
                        font-weight: bold;
                    }
                }
            }
        }

        .dropdown-cart {
            width: 240px !important;
            left: -180%;
        }
    }
}

@media (max-width: 576px) {
    .navbar-menu {
        .logo {
            // display: inline-block !important;
            margin: 0;

            h2 {
                font-size: 25px;
            }
        }

        .navbar-list {
            display: none !important;
        }
    }
}

.slideInDownDrop {
    -webkit-animation-name: slideInDownDrop;
    animation-name: slideInDownDrop;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes slideInDownDrop {
    0% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes slideInDownDrop {
    0% {
        -webkit-transform: translateY(-10%);
        transform: translateY(-10%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

.slideInDownNav {
    -webkit-animation-name: slideInDownNav;
    animation-name: slideInDownNav;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes slideInDownNav {
    0% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes slideInDownNav {
    0% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}