.hotdeal {
    .main-title {
        border-bottom: 1px solid red;
        margin-bottom: 30px;

        .title {
            background-color: crimson;
            color: #fff;
            padding: 5px;
            display: inline-block;

        }
    }

    .carsouls {
        box-sizing: border-box;


    }

    .owl-carousel {
        .owl-item {
            img {
                display: block;
                width: 100%;

            }
        }

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            font-size: 50px !important;

        }

        .owl-prev {
            left: -2rem;
            background: #fff !important;

            span {
                color: rgb(192, 190, 190);
                background: transparent;
                font-size: 5rem;

                &:hover {
                    color: black;
                }
            }

        }

        .owl-next {
            right: -2rem;
            background: #fff !important;

            span {
                color: rgb(192, 190, 190);
                background: white;
                font-size: 5rem;

                &:hover {
                    color: black;
                }
            }
        }
    }


}

