.filter-side {
     
     .laptop-part {
          background-color: #F4F4F4;
          padding: 10px;
          border-radius: 5px;
          .laptop-title{
               
               padding: 0 10px;
               h6{
                    font-size: 1.2rem;
               }
          }
          .accordion {
               .accordion-item {
                    background: inherit !important;
                    border: 0;

                    .accordion-button {
                         background: inherit !important;
                         font-size: .95rem;
                         font-weight: 600;
                    }
                    .accordion-collapse{
                         transition:1s ease;
                         ul{
                              padding-left: 0;
                               li{
                              list-style: none;
                              margin-bottom: 10px;
                         }}
                    }
               }
          }
          
     }
     .filter-part{
          background-color: #F4F4F4;
          padding: 25px;
          border-radius:7px;
          .filter-title{
               margin-bottom: 20px;
          }
          .filter-list{
               padding-left: 0;
               li{
                  list-style: none;
                  margin-bottom: 5px;
               }
          }
     }
}